<template>
	<modal-wrap modalClass="modal-target-settings" @close="close">
		<form action="#!">
			<div class="row">
				<v-combo-select label="Выберите Страну" placeholder="Выберите" :options="optionsCountry"/>
			</div>
			<div class="row">
					<v-combo-select label="Город" placeholder="Выберите" :options="optionsCity"/>
			</div>
			<div class="row">
					<v-control-select label="Пол" :options="sex"/>
			</div>
			<div class="row">
				<p class="select-label">Возраст</p>
				<div class="flex-row">
					<v-text-field
						class="input-wrap"
						type="text"
						placeholder="от"
					/>
					<v-text-field
						class="input-wrap"
						type="text"
						placeholder="до"
					/>
				</div>
			</div>
			<div class="row">
				<v-checkbox label="Только пользователи онлайн" id="online-users"/>
			</div>
			<div class="row">
				<v-text-field
					class="input-wrap"
					type="text"
					label="ID группы VK"
					id="for-vk-group"
					placeholder="например: 123456789"
				/>
				<v-instructions @click="isOpen = !isOpen" text="Где брать ID группы VK?"/>
				<v-button
					class="mt-20"
				>Добавить</v-button>
			</div>
			<div class="row">
				<!--<table-modal class="target-settings-table"></table-modal>-->
			</div>
		</form>
		<modal-instruction id="vkGroup" v-if="isOpen" @close="isOpen = false"/>
	</modal-wrap>
</template>

<script>
	import ModalWrap from './ModalWrap'
	import ModalInstruction from './ModalInstruction'
	import VComboSelect from '../../molecules/inputs/VComboSelect'
	import VControlSelect from '../../molecules/inputs/VControlSelect'
	import VTextField from '../../molecules/inputs/VTextField'
	import VCheckbox from '../../molecules/inputs/VCheckbox'
	import VInstructions from '../info/VInstructions'
	import VButton from '../../molecules/buttons/VButton'
	import TableModal from '../table/TableModal'

  export default {
	  components: {
      ModalWrap,
      ModalInstruction,
      VComboSelect,
      VControlSelect,
      VTextField,
      VCheckbox,
      VButton,
		  VInstructions,
			TableModal
	  },
	  data() {
	    return {
        optionsCountry: ['Moscow', 'Belarus', 'Ukraine'],
        optionsCity: [],
        sex: ['Любой', 'Женский', 'Мужской'],
		    isOpen: false,
      }
	  },
	  methods: {
	    close() {
	      this.$emit('close');
	    }
	  }
  }
</script>

<style lang="scss">
	.modal-target-settings {
		height: auto;
		.row {
			margin-bottom: 20px;
		}
		.select-label {
			margin-bottom: 4px;
		}
		.input-label {
			margin-bottom: 6px;
		}
	}
	.target-settings-table {
		max-height: 100%;
	}
	.flex-row {
		.input-wrap {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	@media screen and (min-width: 812px){
		.flex-row {
			display: flex;
			justify-content: space-between;
			.input-wrap {
				width: 47%;
				margin-bottom: 0;
			}
		}
	}
</style>