<template>
	<div class="combo-select">
		<label :for="id"
		       class="select-label"
		>{{label}}</label>
		<div class="custom-select"
		     :tabindex=tabindex
		     @blur="isOpen = false"
		>
			<span class="toggle-select"
			      :class="{'toggle-select--rotate': isOpen}"
			>
				<svg width="1em" height="1em">
					<use xlink:href="#icon-arrow-select">
						<svg id="icon-arrow-select" viewBox="0 0 20 20">
                <path d="M3.225 5.40832L1.75 6.88332L10 15.1333L18.25 6.88333L16.775 5.40833L10 12.1833L3.225 5.40832Z"
                      fill="currentColor"></path>
            </svg>
					</use>
				</svg>
			</span>
			<div class="selected" @click="isOpen = !isOpen" >
				{{selected}}
			</div>
				<div class="dropdown options"
				     v-show="isOpen">
					<div class="options__search-item">
						<input class="input-search v-text"
						       type="text"
						       @input="searchElem"
						       v-model="searchFilter"
						       placeholder="Поиск"
						>
					</div>
					<div class="options__item"
					     v-for="(option, index) of options"
					     :key="index"
					     @click="selection(option, $event)"
					>
						{{option}}
					</div>
				</div>

		</div>
	</div>
</template>

<script>
  import VTextField from './VTextField'

  export default {
    components: {
      VTextField
    },
	  props: {
      options: Array,
		  tabindex: String,
		  label: String,
		  placeholder: String,
		  id: String,
	  },
    data() {
      return {
        isOpen: false,
        selected: this.options.length ? this.options[0] : 'Выберите город',
	      searchFilter: '',
      }
    },
	  mounted() {
	  },
	  computed: {
      selectedValue() {
        if (selected) return selected
	      else return this.placeholder
      }
	  },
	  methods: {
      selection(option) {
        this.selected = option;
      },
		  searchElem() {
        console.log(this.options);
        this.options.find(this.searchFilter);
        this.selected = this.searchFilter
        // this.options.find(this.searchFilter);
		  }
	  }
  }
</script>

<style lang="scss">
	.combo-select {
		.options {
			padding-bottom: 10px;
		}
		.options__item {
			padding: 10px 25px;
		}
	}
	.options__search-item {
		padding: 10px 15px;
	}
	.select-label {
		color: #fff;
	}

	.input-search {
		border: 1px solid rgba(#fff,.5);
	}
	.custom-select {
		position: relative;
		&:focus {
			border: 1px solid #ffc107;
		}
	}
	.toggle-select {
		position: absolute;
		top: 0;
		right: 0;
	}
	@media screen and (min-width: 812px) {
		.select-label {
			color: #000;
		}
	}

</style>